import ban from '../components/ban.vue';
import jsons from '../../static/json/items.json';
export default {
	data() {
		return {
			ones: ''
		};
	},
	methods: {
		add() {
			this.$store.dispatch('joinstates', this.ones);
		}
	},
	components: {
		ban
	},
	onLoad(res) {
		this.ones = jsons.item.filter(item => {
			return item.id == res.i;
		})[0];
		console.log(this.ones);
	}
};
